* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Cabin", Arial, sans-serif; }

.c-footer {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 288px;
  padding: 30px 100px;
  background-color: #383737; }
  @media only screen and (max-width: 375px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: space-around;
      height: 750px;
      padding: 30px 10px; } }
  @media only screen and (min-width: 376px) and (max-width: 768px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: space-around;
      height: 700px;
      padding: 30px 25px; } }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c-footer {
      flex-flow: wrap;
      justify-content: space-around;
      height: 480px;
      padding: 30px 50px; } }
  .c-footer__logo {
    width: 120px; }
    @media only screen and (min-width: 376px) and (max-width: 768px) {
      .c-footer__logo {
        width: 100px; } }
  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .c-footer__menu-item {
      width: 200px; } }
  @media only screen and (min-width: 376px) and (max-width: 768px) {
    .c-footer__menu-item {
      width: 150px;
      margin-bottom: 20px; } }
  @media only screen and (max-width: 375px) {
    .c-footer__menu-item {
      width: 120px;
      margin-bottom: 20px; } }
  .c-footer__menu-subitem {
    display: block;
    color: #9d9d9e;
    font-size: 16px;
    line-height: 2;
    letter-spacing: 0.02rem;
    text-decoration: none; }
    .c-footer__menu-subitem:first-child {
      color: #fff;
      line-height: 2.25;
      letter-spacing: 0.05rem;
      pointer-events: none; }
    .c-footer__menu-subitem:hover {
      text-decoration: underline; }
  .c-footer__other {
    width: 260px; }
  .c-footer__subscribe {
    margin-bottom: 40px; }
    @media only screen and (max-width: 375px) {
      .c-footer__subscribe-wrapper {
        display: flex;
        justify-content: center; } }
    .c-footer__subscribe-title {
      display: block;
      color: #fff;
      font-size: 16px;
      line-height: 2.25;
      letter-spacing: 0.05rem; }
    .c-footer__subscribe-text {
      display: block;
      margin-bottom: 15px;
      color: #9d9d9e;
      font-size: 16px;
      line-height: 1.25;
      letter-spacing: 0.02rem; }
    .c-footer__subscribe-input-wrapper {
      display: inline-block;
      width: 202px;
      height: 36px;
      margin-right: 10px; }
    .c-footer__subscribe-input {
      width: 100%;
      height: 100%;
      padding: 10px;
      border: 1px solid transparent;
      border-radius: 6px;
      background-color: #fff;
      outline: none;
      color: #383737;
      font-weight: bold;
      font-size: 14px;
      line-height: 1.71;
      letter-spacing: 0.02rem; }
      .c-footer__subscribe-input::placeholder {
        color: #b4bbc2; }
    .c-footer__subscribe-submit-wrapper {
      position: relative;
      display: inline-block;
      width: 36px;
      height: 36px;
      vertical-align: bottom; }
      .c-footer__subscribe-submit-wrapper::before {
        content: '';
        position: absolute;
        top: 50%;
        left: calc(50% - 1px);
        width: 5px;
        height: 5px;
        border-top: solid 2px #fff;
        border-right: solid 2px #fff;
        transform: translate(-50%, -50%) rotate(45deg);
        transform-origin: center; }
    .c-footer__subscribe-submit {
      width: 100%;
      height: 100%;
      border: 1px solid transparent;
      border-radius: 6px;
      background-color: #24aa98;
      outline: none; }
      .c-footer__subscribe-submit:hover {
        border-color: #1a9385;
        background-color: #1a9385;
        cursor: pointer; }
  .c-footer__social-title {
    display: block;
    margin-bottom: 10px;
    color: #fff;
    font-size: 16px;
    line-height: 2.25;
    letter-spacing: 0.05rem; }
  .c-footer__social-item {
    border: 1px solid transparent;
    background-color: transparent;
    outline: none;
    padding: 9px;
    font-size: 14px;
    color: #b4bbc2;
    text-decoration: none; }
    .c-footer__social-item:not(:last-child) {
      margin-right: 10px; }
    .c-footer__social-item:nth-child(2) {
      margin-left: -13px; }
    .c-footer__social-item:hover {
      border-color: #b4bbc2;
      border-radius: 999rem;
      cursor: pointer; }
